import React from 'react';
import PageHeader from '../component/PageHeader';
import PostParagraph from '../component/PostParagraph';
import ReactDisqusComments from 'react-disqus-comments'
//   
const Posts = () => {
    return (
        <div>
            <PageHeader title=" " subtittle="     " Background="" />
            <div className="container">
                <div className="row">
                    <div class="col-lg-8 col-md-10 mx-auto">
                        <PostParagraph title=" " upp="     ." underp="   !" />
                        <PostParagraph upp="     ." />
                        <ReactDisqusComments
                            shortname="nogada-samuso"
                            identifier="nogada-samuso-posts"
                            title="Example Thread"
                            url="https://azxca1731.github.io/#/posts"
                            category_id="123456"
                            onNewComment={this.handleNewComment} />
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Posts;


